import React from 'react'
import Mytab from '../TaoBao/Mytab'
import { connect } from 'react-redux'
import './Cart.css'

class Cart extends React.Component {
    constructor(props)  {
        super(props);
    }
    render(){
        return (
            <div>
                {
                    this.props.collect.map((data,index)=>(
                        <div class='collectImg'>
                            <img src={data} />
                            <button class='btn1' onClick={()=>{
                                this.props.dispatch({type:'DELETE',index:index})
                            }}>删除</button>
                        </div>
                    ))
                }
                <Mytab/>
            </div>
        )
    }
}
const good = (state) => ({
    collect:state.collect
})

export default connect(good)(Cart)


