import React from 'react'
import './Goods.css'
import {NavBar,Icon} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'


class Goods extends React.Component {
    
    constructor(props, context) {
        super(props, context)
        console.info(props)
        this.goodList = props.location
        console.log(this.goodList)
      }
    render() {
        var goodList = {};
        return (
            <div>
                <div>
                    <NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => {
                            this.props.history.push('/home');
                        }}
                    >
                        商品详情页
                    </NavBar>
                </div>

                <div class='img-detail'>
                    <img style={{width:'100%'}} src={this.goodList.src} />
                </div>
                <div class="real-price">
                    <span class="ui-yen ">
                        <i class="price-symbol">¥ </i>
                    <span class="price">{this.goodList.prices}</span><br />
                    <span class='information'>{this.goodList.titles}</span><br />
                    {/* <span class='num'>{this.goodList.dataNum}人已购买</span> */}
                    </span>
                </div>

                <div class="bottom-bar">
                    <div class="btn-shop">
                        <span class="iconfont icon-dianpu" style={{padding:'20%'}}></span>
                        <span class="btn-text">店铺</span>
                    </div>
                    <div class="btn-wangwang">
                        <span class="iconfont icon-kefu" style={{padding:'20%'}}></span>
                        <span class="btn-text">客服</span>
                    </div>
                    <div class="btn-fav" onClick={()=>{
                        this.props.username?this.props.history.push('/goods'):this.props.history.push('/login')
                        this.props.dispatch({
                            type:'ADD',
                            collect:this.goodList.src
                        })
                    }}>
                        <span class="iconfont icon-shoucang" style={{padding:'20%'}}></span>
                        <span class="btn-text">收藏</span>
                    </div>
                    <div class="bottom-bar-btn cart btn-pos-left ">
                        <span class="btn-title">加入购物车</span>
                    </div>
                    <div class="bottom-bar-btn buy btn-pos-right ">
                        <span class="btn-title">立即购买</span>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state)=>({
    collect:state.collect,
    goodname:state.LoginRedux.goodname
})

export default connect(mapStateToProps)(withRouter(Goods))
