import { Carousel } from 'antd-mobile'
import React from 'react'
import { NavLink, Route } from 'react-router-dom';
import './Home.css';
import Mytab from '../TaoBao/Mytab';
import goodinf from '../TaoBao/Content'
import {connect} from 'react-redux'

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
class Home extends React.Component {
    state = {
        data: [
            'https://gw.alicdn.com/imgextra/i1/156/O1CN017qw8dJ1D1UPwEl1dw_!!156-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i1/158/O1CN01LWnzwd1D2PD1NAcCh_!!158-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i3/74/O1CN01UY7Tlw1CPvwP1Rdkx_!!74-0-lubanu.jpg'
        ],
        imgHeight: 176
    };
    constructor(props)  {
        super(props);
    }
    componentDidMount() {
        this.props.dispatch(goodinf());
    }
    render(){
        return (
            <div class='page-container'>
                <NavLink to='/search'>
                    <div class='hhhhhh'>
                        <div class="header-bd">
                            <a class="placeholder">
                                <span class="text iconfont icon-search">
                                    寻找宝贝店铺
                                </span>
                            </a>
                        </div>
                    </div>
                </NavLink>
                <div class='lunbotu'>
                    <Carousel className="space-carousel"
                        autoplay
                        infinite
                        beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                        afterChange={index => this.setState({ slideIndex: index })}
                        >
                        {this.state.data.map((val, index) => (
                            <a
                            key={val}
                            style={{
                                display: 'block',
                                position: 'relative',
                                height: this.state.imgHeight,
                            }}
                            >
                            <img
                                src={`${val}`}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                // fire window resize event to change height
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                                }}
                            />
                            </a>
                        ))}
                    </Carousel>
                </div>
                <div class='recommend-body'>
                {
                    this.props.goodinf.map(dataImg =>(
                        <div class='recommend'>
                            <div class="recommend-item" onClick={()=>{
                                this.props.history.push(
                                    {
                                        pathname:'/Goods',
                                        src:dataImg.img,
                                        prices:dataImg.price,
                                        titles:dataImg.title
                                    }
                                )
                            }}>
                                <a class="recommend-img" href="#"><img class="recommend-img" src={`${dataImg.img}`} /></a>
                                <a class="recommend-info" href="#">
                                    <div class="recommend-title">
                                        <span class="recommend-title-p">{dataImg.title}</span>
                                    </div>
                                    <div class="recommend-price-box">
                                        <span class="recommend-sign recommend-h">￥</span>
                                        <span class="recommend-price">{dataImg.price}</span>
                                    </div>
                                </a>
                            </div>
                        </div>
                    ))
                }
                </div>
                <div class='footer'>
                    <div class="denglu">
                        <a href="#">登录</a>
                        <a href="#">注册</a>
                        <a>手淘App</a>
                        <a href="">用户反馈</a>
                    </div>
                    <div class="fuwu">
                        2018 浙B2-20080224<br/>
                        <a>服务中心</a>
                    </div>
                </div>
                <Mytab />
            </div>
        )
    }
    // routerTo(Content) {
    //     this.props.history.push({pathname: `/Goods/${Content.imgs}`, state: {data: Content}})
    // };
}
const mapStateToProps = (state) =>({
    goodinf:state.ContentRedux.goodinf
})

export default connect(mapStateToProps)(Home)

